.alertify .ajs-dialog {
    background-color: white;
    -webkit-box-shadow: 0px 15px 20px 0px rgba(0, 0, 0, 0.25);
    box-shadow: 0px 15px 20px 0px rgba(0, 0, 0, 0.25);
    border-radius: 2px;
}

.alertify .ajs-header {
    color: black;
    font-weight: bold;
    background: #fafafa;
    border-bottom: #eee 1px solid;
}

.alertify .ajs-body {
    color: black;
}

.alertify .ajs-body .ajs-content .ajs-input {
    display: block;
    width: 100%;
    padding: 8px;
    margin: 4px;
    border-radius: 2px;
    border: 1px solid #CCC;
}

.alertify .ajs-body .ajs-content p {
    margin: 0;
}

.alertify .ajs-footer {
    background: #fbfbfb;
    border-top: #eee 1px solid;
    border-radius: 0 0 2px 2px;
}

.alertify .ajs-footer .ajs-buttons .ajs-button {
    background-color: transparent;
    color: #000;
    border: 0;
    font-size: 14px;
    font-weight: bold;
    text-transform: uppercase;
}

.alertify .ajs-footer .ajs-buttons .ajs-button.ajs-ok {
    color: #3593D2;
}

.alertify-notifier .ajs-message {
    background: rgba(255, 255, 255, 0.95);
    color: #000;
    text-align: center;
}

.alertify-notifier .ajs-message.ajs-success {
    color: settings(font, color, primary);
    background: transparentize(settings(layout, color, success), 0.1);
}

.alertify-notifier .ajs-message.ajs-error {
    color: settings(font, color, primary);
    background: transparentize(settings(layout, color, error), 0.1);
}

.alertify-notifier .ajs-message.ajs-warning {
    color: settings(font, color, primary);
    background: transparentize(settings(layout, color, warning), 0.1);
}
